* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  height: 100vh;
  align-content: center;
}

#drum-machine {
  width: 600px;
  height: 350px;
  border: 1px solid;
  margin: 0 auto;
}

.logo-contenedor {
  height: 10%;
  display: flex;
  justify-content: end;
}

.contenedor-interno {
  padding: 10px;
  height: 90%;
  display: flex;
}

.btns-contenedor {
  width: 60%;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat((3, 1fr));
}
